"use strict";

module.exports = function (ngModule) {
  ngModule.config(addCheckboxType);

  function addCheckboxType(formlyConfigProvider) {
    formlyConfigProvider.setType({
      name: "multiCheckbox",
      template: require("./multiCheckbox.html"),
      wrapper: ["foundationLabel", "foundationHasError"],
      defaultOptions: {
        noFormControl: false
      },
      apiCheck: function (check) {
        return {
          templateOptions: {
            options: check.arrayOf(check.object),
            labelProp: check.string.optional,
            valueProp: check.string.optional
          }
        };
      },
      controller: /* @ngInject */function controller($scope) {
        var to = $scope.to;
        var opts = $scope.options;
        $scope.multiCheckbox = {
          checked: [],
          change: setModel
        };

        // initialize the checkboxes check property
        var modelValue = $scope.model[opts.key];
        if (angular.isArray(modelValue)) {
          (function () {
            var valueProp = to.valueProp || "value";
            angular.forEach(to.options, function (v, index) {
              $scope.multiCheckbox.checked[index] = modelValue.indexOf(v[valueProp]) !== -1;
            });
          })();
        }

        function setModel() {
          $scope.model[opts.key] = [];
          angular.forEach($scope.multiCheckbox.checked, function (checkbox, index) {
            if (checkbox) {
              $scope.model[opts.key].push(to.options[index][to.valueProp || "value"]);
            }
          });
        }
      }
    });
  }
};


